import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CargoResult } from '../../models/results/cargo-result';
import { DetalheCargoPage } from '../detalhe-cargo/detalhe-cargo';

@Component({
    selector: 'page-lista-cargos',
    templateUrl: 'lista-cargos.html',
})
export class ListaCargosPage {

    lista: CargoResult[] = [];

    constructor(public navCtrl: NavController, public navParams: NavParams) {

        this.lista.push({
            nome: 'Presidente',
            descricao: 'O principal papel do presidente  assegurar que as atividades de desenvolvimento profissional e capacitao do clube sejam bem- sucedidas e que seus projetos beneficiem a comunidade.',
            responsabilidades: [
                'Realizar reunies eficientes com agenda detalhada que inclua tempo suficiente para apresentao de relatrios de dirigentes e membros de comisso;',
                'Presidir reunies do conselho diretor;',
                'Indicar todas as comisses permanentes e especiais, com aprovao do conselho diretor, e servir como membro ex- officio em todas elas;',
                'Indicar os presidentes das comisses com base em experincias individuais, e pedir sugestes para membros das respectivas comisses;',
                'Delegar responsabilidades para ajudar os rotaractianos a desenvolverem suas habilidades de liderana e assumirem futuras posies no clube;',
                'Incentivar associados do clube a se envolverem em projetos relacionados a suas habilidades e interesses;',
                'Assegurar que atividades e projetos do clube sejam promovidos e executados com sucesso;',
                'Monitorar o quadro associativo para garantir que haja diversidade de idade e sexo;',
                'Comunicar-se e colaborar com o conselheiro rotariano, associados do Rotary Club patrocinador e administradores distritais de Rotaract, sendo que em clubes sediados em universidade, o presidente deste deve se comunicar com o orientador da rea educacional;',
                'Atualizar as informaes do clube e dados do quadro associativo semestralmente atravs do Portal do Associado.'
            ]
        });

        this.lista.push({
            nome: 'Vice-presidente',
            descricao: 'O principal papel do vice-presidente  apoiar o presidente do clube. Alguns clubes pedem para o presidente entrante servir como vice-presidente.',
            responsabilidades: [
                'Presidir reunies quando o presidente estiver ausente;',
                'Integrar o conselho diretor e servir como membro ex-officio em todas as comisses;',
                'Desempenhar funes especiais designadas pelo presidente;',
                'Estar ciente das metas e atividades do clube;',
                'Assumir a presidncia do clube caso o cargo fique vago'
            ]
        });

        this.lista.push({
            nome: 'Secretrio',
            descricao: 'A principal responsabilidade do secretrio  ajudar o clube a operar com eficincia. O secretrio deve ser bem organizado e saber se comunicar bem.',
            responsabilidades: [
                'Registrar e guardar todas as informaes pertinentes ao clube, incluindo dados do quadro associativo, indicao de membros de comisso, comparecimento a reunies, pagamento de cotas, alm de documentos importantes como certificado de fundao, oramentos e relatrios do clube;',
                'Notificar o Rotary Club patrocinador a respeito de rotaractianos que esto completando 30 anos de idade que podem ser rotarianos em potencial;',
                'Fornecer ao presidente informaes sobre o clube e associados para que este possa manter o RI atualizado;',
                'Preparar a ata das reunies, mostrando as discusses e decises tomadas contendo os seguintes pontos:<br><br><ul><li><span>Data, horrio e local</span></li><li><span>Dirigente presidindo a reunio</span></li><li><span>Membros presentes</span></li><li><span>Aprovao e correo da ata da ltima reunio</span></li><li><span>Declarao do tesoureiro</span></li><li><span>Resumo dos relatrios apresentados por dirigentes e comisses</span></li><li><span>Resumo da agenda (incluindo itens antigos e novos) e decises tomadas</span></li><li><span>Avisos</span></li><li><span>Encerramento</span></li></ul>'
            ]
        });

        this.lista.push({
            nome: 'Tesoureiro',
            descricao: 'O tesoureiro trabalha com o secretrio para manter registros financeiros precisos, devendo ser uma pessoa responsvel e detalhista.<br><br>Ao deixar o cargo no final do ano rotrio, o tesoureiro deve fornecer aos associados do clube e ao tesoureiro entrante um relatrio final, o qual deve fazer parte dos arquivos permanentes do clube',
            responsabilidades: [
                'Presidir a comisso financeira',
                'Coletar as cotas per capita;',
                'Depositar as cotas per capita e verbas provenientes de eventos de arrecadao de fundos;',
                'Pagar as contas e fazer reembolsos de despesas do clube;',
                'Preparar e gerenciar o oramento;',
                'Preparar um relatrio mensal com os seguintes itens:<br><br><ul><li><span>Verbas do clube disponveis no incio e fim do ms</span></li><li><span>Renda e respectiva fonte (como cotas per capita ou evento de arrecadao de fundos)</span></li><li><span>Fazer pagamentos, indicando o item e o recebedor dos fundos</span></li>',
                'Assegurar que o clube cumpra todas as exigncias governamentais referentes a fornecimento de relatrios financeiros.'
            ]
        });

        this.lista.push({
            nome: 'Comisso de Servios Internos',
            descricao: '',
            responsabilidades: [
                'Desenvolver estratgias para recrutar novos associados e manter atuantes os atuais associados',
                'Divulgar as atividades do clube aos associados e pblico em geral',
                'Planejar atividades sociais e de networking',
                'Manter arquivo sobre a histria do clube, incluindo fotografias e descries de atividades do clube'
            ]
        });

        this.lista.push({
            nome: 'Comisso de Servios  Comunidade',
            descricao: '',
            responsabilidades: [
                'Analisar sugestes dos associados para projetos comunitrios, assegurando que atendam a necessidades reais e que possam ser realizados com sucesso pelo clube',
                'Planejar o projeto anual comunitrio do clube',
                'Liderar, organizar e implementar o projeto comunitrio'
            ]
        });

        this.lista.push({
            nome: 'Comisso de Servios Internacionais',
            descricao: '',
            responsabilidades: [
                'Analisar sugestes dos associados para projetos internacionais, assegurando que atendam a necessidades reais e que possam ser realizados com sucesso pelo clube',
                'Planejar o projeto anual internacional do clube',
                'Liderar, organizar e implementar o projeto internacional',
                'Desenvolver atividades adicionais que promovam a compreenso internacional entre associados e comunidades'
            ]
        });

        this.lista.push({
            nome: 'Comisso de Desenvolvimento Profissional',
            descricao: '',
            responsabilidades: [
                'Pedir aos associados sugestes para atividades de desenvolvimento profissional',
                'Revisar e planejar atividades',
                'Descobrir recursos na comunidade',
                'Liderar, organizar e implementar as atividades'
            ]
        });

        this.lista.push({
            nome: 'Comisso de Finanas',
            descricao: '',
            responsabilidades: [
                'Decidir de que forma o clube ir financiar suas atividades',
                'Esforar-se para manter as cotas per capita baixas e o clube financeiramente autossuficiente',
                'Coletar cotas do distrito e clube',
                'Conseguir doaes monetrias e em espcie de empresas e organizaes'
            ]
        });
    }

    abrirDetalhe(cargo: CargoResult) {
        this.navCtrl.push(DetalheCargoPage, { cargo: cargo });
    }
}



